import React from 'react'
import {Redirect, Link} from 'react-router-dom'
import reactCookie from 'react-cookies'


class Signup extends React.Component {

    constructor (props) {
        super(props)
        this.signup = this.signup.bind(this)
        if (reactCookie.load('UID') !== undefined) {
            this.state = {
                redirect: '/'
            }
        } else {
            this.state = {
                redirect: 'default'
            };
        }
    }

    render () {
        if (this.state.redirect === 'login') {
            return (
                <div id='signup-info'>            
                    <Redirect to='/login'></Redirect>
                </div>
            )
        } else if (this.state.redirect === '/') {
            return (

                <div id='signup-info'>
                    <Redirect to='/'></Redirect>
                </div>
            )
        } else {
            return (
                <div id='signup-info'>            
                    Enter Email: <input id='email' type='text'></input> <br />
                    Enter name: <input id='name' type='text'></input> <br />
                    Enter Password: <input id='password' type='password'></input> <br />
                    <input type='submit' onClick={this.signup}></input> <br />
                    <button><Link to='/login'>Have an account? Login</Link></button>                    
                </div>
            )
        }
    };
    
    signup () {
        const req = new XMLHttpRequest()
        req.addEventListener('load', () => {
            if (req.responseText === 'ok') {
                this.setState({redirect: 'login'}) // Redirect after a successful status from server
            } else {
                window.alert('Perhaps the email is invalid or in use')
            }
        })
        req.open('POST', 'http://localhost:4000/signup')
        req.setRequestHeader('Content-Type', 'application/json')
        req.send(JSON.stringify({ 
            email: document.getElementById('email').value,
            name: document.getElementById('name').value, 
            password: document.getElementById('password').value
        }))
    
    }
    componentWillUnmount() {
        delete this.state.redirect
    }
}


export default Signup