import React from 'react'
import {Redirect} from 'react-router-dom'
import reactCookie from 'react-cookies'

class Login extends React.Component {
    constructor (props) {
        super(props)
        this.login = this.login.bind(this)
        if (reactCookie.load('UID') !== undefined) {
            this.state =  {
                login: true
            }
        } else {

            this.state = {
                login: false
            }
        }
    }

    render () {
        if (this.state.login) {
            return (
                <div id='signup-info'>            
                    <Redirect to='/'></Redirect>
                </div>
            )
        } else {
            return (
                <div id='signup-info'>            
                    Enter Email: <input id='email' type='text'></input> <br />
                    Enter Password: <input id='password' type='password'></input> <br />
                    <input type='submit' onClick={this.login}></input> <br />                   
                </div>
            )
        }
    }

    login () {
        const req = new XMLHttpRequest()
        req.addEventListener('load', () => {
            if (req.responseText === 'ok') {
                this.setState({login: true})
            } else {
                window.alert('Enter proper credentials')
            }
        })
        req.open('POST', 'http://localhost:4000/login')
        req.setRequestHeader('Content-Type', 'application/json')
        req.send(
            JSON.stringify({
                email: document.getElementById('email').value,
                password: document.getElementById('password').value
            })
        )
    }
}


export default Login