import Recipient from './recipient'
import MessageWall from './MessageWall'
import MessagePrompt from './MessagePrompt'

function MessageRoom (props) {
    return (
        <div className='message-room col-8 border-start border-success border-curve-10'>
            <Recipient recipientName={props.recipientName}></Recipient>
            <MessageWall messages={props.messages}></MessageWall>
            <MessagePrompt sendMessage={props.sendMessage}></MessagePrompt>
        </div>
    )
}

export default MessageRoom