import React from 'react'


class Search extends React.Component {
    
    constructor (props) {
        super(props)
        this.add = this.add.bind(this)
    }

    render() {
        return (
            <div className='container d-flex row gx-4'>
                <div className='col row'>
                    <input className='input-prompt border-curve-10' type='text' id='email'></input>
                </div>
                <div className='col-1'>
                    <button className='btn btn-primary border-curve-10' onClick={this.add}>+</button>
                </div>                
            </div>
        )
    }

    add() {
        this.props.addFriend(document.getElementById('email'))
    }
}

export default Search