import React from 'react'
import {Redirect} from 'react-router-dom'

class Logout extends React.Component {
    
    constructor (props) {
        super(props)
        this.logout = this.logout.bind(this)
        this.state = {pressed: false}
    }
    
    render () {
        console.log(this.props.account)
        if (this.state.pressed) {
            return (
                <Redirect to='/login'></Redirect>
            )
        } else {
            return (
                <div className='border-top border-success border-curve-10 h-10-percent d-flex align-items-center justify-content-center'>
                    <div className='col'>
                        <h6 className='d-flex justify-content-center' id='account-name'>{this.props.account.name}</h6>
                    </div>
                    <div className='col d-flex justify-content-center'>
                        <button className='btn btn-primary' onClick={this.logout}>Logout</button>
                    </div>
                </div>
            )
        }
    }

    logout () {
        const req = new XMLHttpRequest()
        req.addEventListener('load', () => {
            if (req.response === 'ok') {
                this.setState({pressed: true})
            }
        })
        req.open('GET', 'http://localhost:4000/logout')
        req.send()
    }

}


export default Logout