import React from 'react'
import FriendFrame from './FriendFrame'

class List extends React.Component {
    

    render() {

        return (
            <div className='d-flex row h-80-percent-oflow'>
                <div>
                    <FriendFrame name='friend 1' message_count='4' changeCurrentRecipient={this.props.changeCurrentRecipient} email='friend_1@gmail.com'></FriendFrame>
                    <FriendFrame name='friend 2' message_count='4' changeCurrentRecipient={this.props.changeCurrentRecipient} email='friend_2@gmail.com'></FriendFrame>
                    <FriendFrame name='friend 3' message_count='4' changeCurrentRecipient={this.props.changeCurrentRecipient} email='friend_3@gmail.com'></FriendFrame>

                </div>
            </div>
        )
    }

}


export default List