
function MessageBubble (props) {
    if (props.data.type === 'outgoing') {
        return (
            <div className='row m-2 justify-content-end'>
                <div className='col-5 border-top border-bottom border-success border-curve-10'>
                    <div className='p-1'>{props.data.message}</div>
                    <div className='row justify-content-end message-time p-1'>{props.data.time}</div>
                </div>
            </div>
        )
    } else {
        return (
            <div className='row m-2 justify-content-end'>
                <div className='col-5 border-top border-bottom border-success border-curve-10'>
                    <div className='p-1'>{props.data.message}</div>
                    <div className='row justify-content-end message-time p-1'>{props.data.time}</div>
                </div>
            </div>
        )
    }
    
}

export default MessageBubble