import MessGallery from './messenger/MessGallery'
import Signup from './signup/Signup'
import Login from './login/Login'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'

function App () {
    return (
        <Router>
            <div className='container h-100 d-flex justify-content-center align-items-center'>
                <Switch>
                    <Route exact path='/'>
                        <MessGallery></MessGallery>
                    </Route>
                    <Route path='/signup'>
                        <Signup></Signup>
                    </Route>
                    <Route path='/login'>
                        <Login></Login>
                    </Route>
                </Switch>
            </div>
        </Router>
    )
}

export default App