import React from 'react'

class Prompt extends React.Component {

    constructor (props) {
        super(props)
        this.send = this.send.bind(this)
    }

    render () {
        return (
            <div className='container d-flex row gx-4'>
                <div className='col row'>
                    <input className='input-prompt border-curve-10' id='message' type='text'></input>
                </div>
                <div className='col-1'>
                    <button className='btn btn-primary border-curve-10' onClick={this.send}>^</button>
                </div>
            </div>
        )
    }

    send () {
        this.props.sendMessage(
            document.getElementById('message').value
        )
    }
}

export default Prompt